@font-face {
    font-family: 'Uber Move Text Regular';
    font-style: normal;
    font-weight: normal;
    src: local('Uber Move Text Regular'), url('UberMoveTextRegular.woff') format('woff');
    }

$enable-rounded: false;
$dark: $black;
$font-family-sans-serif:      'Uber Move Text Regular', system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
